<template>
  <div>
    <v-divider></v-divider>
    <v-card class="mb-4 elevation-0">
      <v-card-text>
        <v-layout row wrap d-flex pa-2>
          <v-flex xs12 pa-2>
            <v-subheader>Dirección Personal</v-subheader>
            <v-form ref="direction_form" lazy-validation>
              <v-flex d-flex>
                <v-text-field
                  xs12
                  md6
                  required
                  clearable
                  label="Calle y número"
                  :rules="rules.street"
                  v-model="direction.street"
                ></v-text-field>
                <v-select :items="states" required label="Estado" v-model="direction.state"></v-select>
              </v-flex>
              <v-flex d-flex>
                <v-text-field
                  xs12
                  md6
                  required
                  clearable
                  label="Colonia"
                  :rules="rules.suburb"
                  v-model="direction.suburb"
                ></v-text-field>
                <v-text-field
                  xs12
                  md6
                  required
                  clearable
                  label="Código Postal"
                  :rules="rules.postal_code"
                  v-model="direction.postal_code"
                ></v-text-field>
              </v-flex>
            </v-form>
          </v-flex>
        </v-layout>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn class="mr-4" depressed color="primary" @click="submit">Guardar</v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
export default {
  name: "Direction",
  props: {
    direction: {
      type: Object,
      default: function() {
        return {
          id: "",
          street: "",
          state: "",
          suburb: "",
          postal_code: ""
        };
      }
    }
  },
  methods: {
    async submit() {
      if (this.$refs.direction_form.validate() === true) {
        try {
          let response = await this.$http.patch(
            "profile/" + this.direction.id + "/direction",
            this.direction
          );

          let profile = response.data.data;
          console.log(profile);
          
          this.$store.state.user.profile = profile;
          this.$store.dispatch("toast_me", {
            appear: true,
            title: "Dirección. ",
            message: "Actualizada con éxito",
            color: "primary"
          });
        } catch (error) {
          console.log(error);
        }
      } else {
        console.log("Aun hay faltantes");
      }
    }
  },
  data: () => ({
    states: [
      "CDMX",
      "Aguascalientes",
      "Baja California",
      "Baja California Sur",
      "Campeche",
      "Chiapas",
      "Chihuahua",
      "Coahuila",
      "Colima",
      "Durango",
      "Estado de México",
      "Guanajuato",
      "Guerrero",
      "Hidalgo",
      "Jalisco",
      "Michoacán",
      "Morelos",
      "Nayarit",
      "Nuevo León",
      "Oaxaca",
      "Puebla",
      "Querétaro",
      "Quintana Roo",
      "San Luis Potosí",
      "Sinaloa",
      "Sonora",
      "Tabasco",
      "Tamaulipas",
      "Tlaxcala",
      "Veracruz",
      "Yucatán",
      "Zacatecas"
    ],
    rules: {
      street: [v => !!v || "Calle y número obligatorios"],
      state: [v => !!v || "Estado obligatorio"],
      suburb: [v => !!v || "Colonia obligatoria"],
      postal_code: [v => !!v || "Código postal obligatorio"]
    }
  })
};
</script>
