<template>
  <v-dialog v-model="open" persistent max-width="700px">
    <v-card class="pa-2">
      <v-card-title class="headline">
        <strong>{{ type.selected.name }}</strong>
      </v-card-title>
      <v-card-text>
        <v-container grid-list-md pa-1>
          <v-form ref="guardForm" lazy-validation>
            <v-layout wrap>
              <v-flex xs12>
                <v-subheader>Tipo de evento</v-subheader>
              </v-flex>
              <v-select
                :items="type.available"
                v-model="type.selected"
                prepend-icon="event_available"
                item-text="name"
                return-object
                label="Tipo"
                required
                hint="Se registra como guardia o evento y se puede filtrar en el calendario"
              ></v-select>
              <v-flex xs12>
                <v-subheader>General</v-subheader>
              </v-flex>
              <v-flex xs12 md6>
                <v-menu
                  v-model="datePickerOpen"
                  :close-on-content-click="false"
                  :nudge-right="20"
                  lazy
                  transition="scale-transition"
                  offset-y
                  full-width
                  min-width="290px"
                  required
                >
                  <template v-slot:activator="{ on }">
                    <v-text-field
                      v-model="guardDate"
                      label="Fecha de guardia"
                      prepend-icon="event"
                      readonly
                      v-on="on"
                    ></v-text-field>
                  </template>
                  <v-date-picker v-model="guardDate" @input="datePickerOpen = false"></v-date-picker>
                </v-menu>
              </v-flex>
              <v-flex xs12 md6>
                <v-menu
                  ref="timePicker"
                  v-model="timePickerOpen"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  :return-value.sync="guardTime"
                  lazy
                  transition="scale-transition"
                  offset-y
                  full-width
                  max-width="290px"
                  min-width="290px"
                  required
                >
                  <template v-slot:activator="{ on }">
                    <v-text-field
                      v-model="guardTime"
                      label="Hora"
                      prepend-icon="access_time"
                      readonly
                      v-on="on"
                    ></v-text-field>
                  </template>
                  <v-time-picker
                    v-if="timePickerOpen"
                    v-model="guardTime"
                    full-width
                    @click:minute="$refs.timePicker.save(guardTime)"
                  ></v-time-picker>
                </v-menu>
              </v-flex>
              <v-flex xs12>
                <v-text-field
                  hint="Horas intermedias están permitidas. Ej. 1.5 hrs"
                  label="Duración"
                  type="number"
                  v-model="duration"
                  suffix="hrs"
                  min="0"
                  :rules="[v => v > 0 || 'La duración debe ser mayor a una hora']"
                  required
                ></v-text-field>
              </v-flex>
              <v-divider></v-divider>
              <v-subheader>Personal</v-subheader>
              <v-flex xs12>
                <v-select
                  :items="c_operator"
                  v-model="users.operator"
                  prepend-icon="event_seat"
                  label="Operador"
                  return-object
                  hint="Muestra solo los usuarios con bandera de operador"
                >
                  <template v-slot:selection="data">{{ data.item.email }}</template>
                  <template v-slot:item="data">
                    {{ data.item.profile.name }} {{ data.item.profile.appat }} ({{ data.item.email }})
                    <template
                      v-if="user.email === data.item.email"
                    >
                      <v-icon color="red" right>star</v-icon>
                    </template>
                  </template>
                </v-select>
              </v-flex>
              <v-flex xs12>
                <v-select
                  :items="c_service_chief"
                  v-model="users.service_chief"
                  prepend-icon="accessibility_new"
                  return-object
                  label="Jefe de servicio"
                  hint="Muestra solo los usuarios con bandera de jefe de servicio"
                >
                  <template v-slot:selection="data">{{ data.item.email }}</template>
                  <template v-slot:item="data">
                    {{ data.item.profile.name }} {{ data.item.profile.appat }} ({{ data.item.email }})
                    <template
                      v-if="user.email === data.item.email"
                    >
                      <v-icon color="red" right>star</v-icon>
                    </template>
                  </template>
                </v-select>
              </v-flex>
              <v-flex xs12>
                <v-subheader>Tripulación</v-subheader>
              </v-flex>
              <v-flex xs12>
                <v-select
                  :items="users.personel_quota"
                  v-model="users.personel"
                  label="Número de personal"
                  @change="extra = 0; users.personel_selected = []"
                ></v-select>
              </v-flex>
              <v-flex xs12 v-for="i in users.personel" :key="i">
                <v-select
                  prepend-icon="face"
                  :label="'Personal ' + i"
                  v-model="users.personel_selected[i - 1]"
                  :items="c_personel[i-1]"
                  hint="Mostrando todos los usuarios menos los elegidos previamente"
                >
                  <template v-slot:selection="data">{{ data.item.email }}</template>
                  <template v-slot:item="data">
                    {{ data.item.profile.name }} {{ data.item.profile.appat }} ({{ data.item.email }})
                    <template
                      v-if="user.email === data.item.email"
                    >
                      <v-icon color="red" right>star</v-icon>
                    </template>
                  </template>
                </v-select>
              </v-flex>
              <v-divider></v-divider>
              <v-flex xs12>
                <v-subheader>Unidad</v-subheader>
              </v-flex>
              <v-flex xs8 md8>
                <v-select
                  :items="units.available"
                  v-model="units.selected"
                  prepend-icon="local_shipping"
                  item-text="name"
                  return-object
                  label="Unidad"
                  required
                  hint="Mostrando las unidades activas."
                ></v-select>
              </v-flex>
              <v-flex xs4 md>
                <v-text-field
                  hint="Personas extra que podrían suscribirse"
                  label="Extras (Personal)"
                  type="number"
                  :max="(3 - users.personel)"
                  min="0"
                  :disabled="((3 - users.personel) != 0) ? false : true "
                  v-model="extra"
                  prepend-icon="group_add"
                ></v-text-field>
              </v-flex>
              <v-divider></v-divider>
              <v-subheader>Extras</v-subheader>
              <v-flex xs12>
                <v-textarea
                  name="description"
                  label="Descripción del guardia/evento"
                  v-model="description"
                  counter
                  maxlength="250"
                  hint="Breve descripción de lo que se va a realizar en la guardia/evento."
                ></v-textarea>
              </v-flex>
              <v-flex>
                <v-switch v-model="isActive" label="Habilita la suscripción a esta guardia."></v-switch>
              </v-flex>
              <!-- <div>
                <div v-for="(el,i) in still_available" :key="i">{{ el.email }}</div>
              </div>-->
            </v-layout>
          </v-form>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="red darken-1" flat @click="closeDialog">Close</v-btn>
        <v-btn color="blue darken-1" flat @click="saveAndExit">Save</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import moment from "moment";
import { mapState } from "vuex";

export default {
  name: "NewGuardDialog",
  props: {
    open: {
      type: Boolean,
      default: false
    }
  },
  data: () => ({
    type: {
      available: [
        { name: "Evento", value: "event" },
        { name: "Guardia", value: "guard" }
      ],
      selected: { name: "Guardia", value: "guard" }
    },
    guardDate: moment().format("YYYY-MM-DD"),
    datePickerOpen: false,
    guardTime: moment().format("HH:mm"),
    timePickerOpen: false,
    duration: 0,
    description: "",
    units: {
      available: [],
      selected: []
    },
    users: {
      globallyAvailable: [],
      operator: {},
      service_chief: {},
      personel: 0,
      personel_quota: [0, 1, 2, 3],
      personel_selected: []
    },
    isActive: false,
    extra: 0
  }),
  computed: {
    ...mapState({
      user: "user"
    }),
    still_available() {
      let totalUsed = [];
      if (Object.keys(this.users.operator).length > 0) {
        totalUsed.push(this.users.operator);
      }

      if (Object.keys(this.users.service_chief).length > 0) {
        totalUsed.push(this.users.service_chief);
      }

      if (this.users.personel_selected.length > 0) {
        this.users.personel_selected.forEach(el => {
          totalUsed.push(el);
        });
      }
      return this.users.globallyAvailable.filter(el => totalUsed.includes(el));
    },
    c_operator() {
      let removed = this.still_available.filter(
        el => el.email != this.users.operator.email
      );
      let operators = this.users.globallyAvailable.filter(
        el => !removed.includes(el)
      );
      operators.filter(el => el.operator === 1);
      return operators;
    },
    c_service_chief() {
      let removed = this.still_available.filter(
        el => el.email != this.users.service_chief.email
      );
      let operators = this.users.globallyAvailable.filter(
        el => !removed.includes(el)
      );
      operators.filter(el => el.service_chief === 1);
      return operators;
    },
    c_personel() {
      let individual = [];

      for (let index = 0; index < this.users.personel; index++) {
        let removed = this.still_available;
        if (typeof this.users.personel_selected[index] !== "undefined") {
          removed = this.still_available.filter(
            el => el.email != this.users.personel_selected[index].email
          );
          let temp = this.users.globallyAvailable.filter(
            el => !removed.includes(el)
          );
          individual.push(temp);
        } else {
          individual.push(
            this.users.globallyAvailable.filter(el => !removed.includes(el))
          );
        }
      }
      return individual;
    }
  },
  async created() {
    await this.fetchUnits();
    await this.fecthUsers();
  },
  updated() {
    // this.$refs.guardForm.reset();
  },
  methods: {
    async fetchUnits() {
      try {
        let units = await this.$http.get("units");
        this.units.available = units.data.data;
      } catch (error) {
        console.log(error);
      }
    },
    async fecthUsers() {
      try {
        let usersWithProfile = await this.$http.get("guards/users");
        this.users.globallyAvailable = usersWithProfile.data.data.filter(
          el => el.email != "admin@suuma.com.mx"
        );
      } catch (error) {
        console.log(error);
      }
    },
    closeDialog() {
      this.clean();
      this.$emit("dismiss");
    },
    async saveAndExit() {
      if (this.$refs.guardForm.validate() == false) {
        return;
      }

      let personel = [];

      let guard = {
        type: this.type.selected.value,
        date: this.guardDate.replace(/\-/g, "/"),
        time: this.guardTime,
        duration: parseFloat(this.duration),
        unit_id: this.units.selected.id,
        max_quota: 2 + this.users.personel_selected.length + parseInt(this.extra), // 2 (OPERATOR & SERVICE) + SELECTED PERSONAL + EXTRA...
        user_id: this.$store.state.user.id,
        description: this.description,
        isActive: this.isActive,
        crew: []
      };

      if (Object.keys(this.users.operator).length > 0) {
        personel.push({ role: "operator", id: this.users.operator.id });
      }

      if (Object.keys(this.users.service_chief).length > 0) {
        personel.push({
          role: "service_chief",
          id: this.users.service_chief.id
        });
      }

      if (this.users.personel_selected.length > 0) {
        this.users.personel_selected.forEach(el => {
          personel.push({ role: "personal", id: el.id });
        });
      }

      guard.crew = personel;

      this.$emit('save', guard);
      // let newGuard = await this.$http.post("guards", guard);
      

    },
    clean() {
      this.guardDate = "";
      this.guardTime = "";
      this.duration = 0;
      (this.units.selected = {}), (this.extra = 0);
      this.description = "";
      this.users.operator = {};
      this.users.service_chief = {};
      this.users.personel = 0;
      this.users.personel_selected = [];
    }
  }
};
</script>
