<template>
  <div class="default">
    <isLoading :value="loadingPage.appear" :message="loadingPage.message"></isLoading>
    <side-nav-bar></side-nav-bar>
    <v-content>
      <v-container fluid class="mainView">
        <alert-nc
          :appear="alert_state.appear"
          :alertType="alert_state.type"
          :message="alert_state.message"
          :timeout="alert_state.timeout"
        ></alert-nc>
        <toast
          :appear="toast.appear"
          :color="toast.color"
          :text="toast.message"
          :timeout="toast.timeout"
          :title="toast.title"
        ></toast>
        <breadcrumb class="mt-2 ml-3 mb-0"></breadcrumb>
        <router-view class="mainContent"></router-view>
      </v-container>
    </v-content>
  </div>
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";
import Menu from "../components/Menu.vue";
import NoConnection from "../components/Alerts/NoConnection";
import Loading from "../pages/default/Loading";
import Toast from "../components/Alerts/Toast";
import Breadcrumb from "../components/Breadcrumb";

export default {
  computed: {
    ...mapState({
      alert_state: "alert",
      toast: "toast",
      loadingPage: "loadingPage"
    })
  },
  async created() {
    if (this.$store.state.jwt === "") return;
    if (
      this.$store.state.photo === null ||
      Object.keys(this.$store.state.photo).length === 0
    ) {
      await this.$store.dispatch("fetch_photo");
    }

    if (this.$store.state.user.profile.name != null) {
      this.$store.dispatch("toast_me", {
        appear: true,
        message:
          this.$store.state.user.profile.name.split(" ")[0] +
            " " +
            this.$store.state.user.profile.appat +
            "!" || "Usuario",
        color: "grey darken-4",
        title: "¡Bievenido"
      });
    }
  },
  methods: {
    ...mapMutations({
      changeAlert: "CHANGE_ALERT"
    }),
    ...mapActions({
      apiActive: "isApiActive"
    }),
    async updateStatusAPI() {
      try {
        let res = await this.apiActive();
        if (res) {
          if (res.data.status != "OK") {
            this.changeAlert({
              appear: true,
              message: "El API está conectado pero con problemas."
            });
          } else {
            this.changeAlert({
              appear: true,
              message: "Conectado a SUUMA API."
            });
          }
        }
      } catch (error) {
        let errorStatus = null;
        if (!error.response) {
          errorStatus = "Error: No hay conexión al API.";
        } else {
          errorStatus = error.response.data.message;
        }
        this.changeAlert({
          appear: true,
          message: errorStatus
        });
      }
    }
  },
  components: {
    "side-nav-bar": Menu,
    "alert-nc": NoConnection,
    breadcrumb: Breadcrumb,
    isLoading: Loading,
    toast: Toast
  }
};
</script>

