import Vue from 'vue';
import Router from 'vue-router';
import Home from '../pages/Home.vue';
import store from '../store';
import SessionUtil from '../session';
import moment from 'moment';
// Fix
import Profile from '../pages/Profile/Profile.vue';
// import Guards from '../pages/Guardias.vue';
import Approvals from '../pages/Aprobar/Default.vue';
import UserApprovals from '../pages/Aprobar/Usuarios.vue';
import About from '../pages/About.vue';
import Register from '../pages/Register/Register.vue';
import Login from '../pages/Login/Login.vue';
import NotFound from '../pages/default/404.vue';
import Unauthorized from '../pages/default/Unauthorized.vue';
import Guards from '../pages/Guardias/Guards.vue';

Vue.use(Router);

let router = new Router({
    mode: 'history',
    routes: [{
            path: '/',
            name: 'Home',
            meta: {
                layout: 'default'
            },
            component: Home,
            meta: {
                requiresAuth: true,
                requiresAdmin: false
            }
        },
        {
            path: '/perfil',
            name: 'Perfil',
            component: Profile,
            meta: {
                requiresAuth: true,
                requiresAdmin: false
            }
        },
        {
            path: '/guardias',
            name: 'Guardias',
            component: Guards,
            meta: {
                requiresAuth: true,
                requiresAdmin: false
            }
        },
        {
            path: '/aprobar',
            name: 'Aprobaciones',
            component: Approvals,
            meta: {
                requiresAuth: true,
                requiresAdmin: true
            },
            children: [{
                path: 'usuarios',
                name: 'Aprobacion_usuarios',
                component: UserApprovals,
                meta: {
                    requiresAuth: true,
                    requiresAdmin: true
                }
            }]
        },
        {
            path: '/acerca',
            name: 'About',
            meta: {
                layout: 'default',
                requiresAuth: true
            },
            component: About,
        },
        {
            path: '/registro',
            name: 'Registro',
            meta: {
                layout: 'login'
            },
            component: Register
        },
        {
            path: '/login',
            name: 'Login',
            meta: {
                layout: 'login'
            },
            component: Login
        },
        {
            path: '/404',
            name: "Not-Found",
            meta: {
                layout: 'login'
            },
            component: NotFound
        },
        {
            path: '/401',
            name: "Unauthorized",
            meta: {
                layout: 'login'
            },
            component: Unauthorized
        },
        {
            path: '*',
            redirect: '/404'
        }
    ]
});

router.beforeEach((to, from, next) => {
    store.commit('CHANGE_ALERT', {
        appear: false
    });
    if (to.matched.some(record => record.meta.requiresAuth)) {
        if (store.getters.isLoggedIn) {
            retrieveTime();
            if (to.matched.some(record => record.meta.requiresAdmin)) {
                let isAdmin = store.getters.getRoles.some(group => group === 'admin');
                if (isAdmin == false) {
                    next('/401');
                }
            }
            next()
            return
        }
        next('/login');
    }
    next();
});



var timer_fn;
Vue.prototype.timer_fn = null;

function retrieveTime() {
    let expiration = new SessionUtil(store.state.jwt);
    let now = moment();
    let exp = expiration.getExpiration();
    let stillActive = moment(now).isSameOrBefore(exp);
    if (!stillActive && (!Number.isNaN(exp))) {
        store.dispatch("unset_token");
    } else {
        store.commit('set_exp', exp.diff(now))
        // changeTime(exp.diff(now));
        window.setTimeout(timer, 1000);
    }
}

function timer() {
    window.clearTimeout(Vue.prototype.timer_fn);
    store.commit('subs_exp', 1000);
    // changeTime(1000);
    Vue.prototype.timer_fn = window.setTimeout(timer, 1000);
}

export default router;