<template>
  <div>
    <v-divider></v-divider>
    <v-card class="mb-4 elevation-0">
      <v-card-text>
        <v-layout row wrap d-flex pa-2>
          <v-flex xs12 pa-2>
            <v-subheader>Fiscal</v-subheader>
            <p
              class="ma-2"
            >Este apartado puede quedar vacío sino desea agregar información para facturación.</p>
            <v-form ref="fiscal_form" lazy-validation>
              <v-flex d-flex>
                <v-text-field
                  xs12
                  md6
                  clearable
                  label="Razón Social"
                  v-model="fiscal.business_name"
                ></v-text-field>
                <v-text-field xs12 md6 clearable label="Dirección" v-model="fiscal.direction"></v-text-field>
              </v-flex>
              <v-flex d-flex>
                <v-text-field xs12 md6 clearable label="RFC" v-model="fiscal.RFC"></v-text-field>
                <v-text-field
                  xs12
                  md6
                  clearable
                  label="Email"
                  :rules="[v => /.+@.+/.test(v) || 'Ingresa un correo válido']"
                  type="email"
                  v-model="fiscal.email"
                ></v-text-field>
              </v-flex>
            </v-form>
          </v-flex>
        </v-layout>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn class="mr-4" depressed color="primary" @click="submit">Guardar</v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
export default {
  name: "Direction",
  props: {
    fiscal: {
      type: Object,
      default: function() {
        return {
          id: "",
          business_name: "",
          direction: "",
          RFC: "",
          email: ""
        };
      }
    }
  },
  methods: {
    async submit() {
      let t1, t2, t3, t4;
      t1 =
        this.fiscal.business_name != null || this.fiscal.business_name == ""
          ? true
          : false;
      t2 =
        this.fiscal.direction != null || this.fiscal.direction == ""
          ? true
          : false;
      t3 = this.fiscal.RFC != null || this.fiscal.RFC == "" ? true : false;
      t4 = this.fiscal.email != null || this.fiscal.email == "" ? true : false;
      console.log(t1, t2, t3, t4);

      if (!t1 && !t2 && !t3 && !t4) {
        this.$store.dispatch("toast_me", {
          appear: true,
          message: "Sin facturación.",
          color: "gray darken-5"
        });
      }
      let b1, b2, b3, b4;
      b1 = t1 ? 1 : 0;
      b2 = t2 ? 1 : 0;
      b3 = t3 ? 1 : 0;
      b4 = t4 ? 1 : 0;
      let flag = b1 + b2 + b3 + b4;
      if (flag != 0) {
        if (flag < 4 && flag > 0) {
          this.$store.dispatch("toast_me", {
            appear: true,
            title: "Forma modificada.",
            message: "Si se modifica algo, complete la forma de facturación.",
            color: "error"
          });
          return;
        }
      }
      try {
        console.log(this.fiscal);

        let response = await this.$http.patch(
          "profile/" + this.fiscal.id + "/fiscal",
          this.fiscal
        );

        let profile = response.data.data;
        console.log(profile);

        this.$store.state.user.profile = profile;
        this.$store.dispatch("toast_me", {
          appear: true,
          title: "Facturación. ",
          message: "Actualizada con éxito",
          color: "primary"
        });
      } catch (error) {
        console.log(error);
      }
    }
  },
  data: () => ({
    rules: {}
  })
};
</script>
