<template>
  <div>
    <v-divider></v-divider>
    <v-card class="mb-4 elevation-0">
      <v-card-text>
        <v-layout row wrap d-flex pa-2>
          <v-flex xs12 pa-2>
            <v-subheader>Información complementaria</v-subheader>
            <v-form ref="extras_form" lazy-validation>
              <v-flex d-flex xs12>
                <v-flex xs12 md6>
                  <v-select
                    persistent-hint
                    hint="Generación SUUMA"
                    reverse
                    solo
                    dense
                    flat
                    :value="extras.generation"
                    :items="generations"
                    label="Generación"
                    v-model="extras.generation"
                  ></v-select>
                </v-flex>
                <v-flex xs12 md6>
                  <v-menu
                    v-model="datePicker"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                  >
                    <template v-slot:activator="{ on }">
                      <v-text-field
                        required
                        v-model="extras.exp_BLS"
                        label="Fecha de vencimiento BLS"
                        prepend-icon="event"
                        hint="DD/MM/AAAA"
                        :rules="rules.date"
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <v-date-picker
                      xs12
                      md6
                      type="month"
                      v-model="dummyDate"
                      @input="datePicker = false"
                    ></v-date-picker>
                  </v-menu>
                </v-flex>
              </v-flex>
            </v-form>
          </v-flex>
        </v-layout>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn class="mr-4" depressed color="primary" @click="submit">Guardar</v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
export default {
  name: "Extras",
  props: {
    extras: {
      type: Object,
      default: function() {
        return {
          id: "",
          generation: "",
          exp_BLS: ""
        };
      }
    }
  },
  data: () => ({
    datePicker: false,
    dummyDate: "",
    rules: {
      date: [v => !!v || "Fecha de nacimiento es obligatoria"]
    }
  }),
  computed: {
    generations() {
      return [...Array(50).keys()].reverse();
    }
  },
  watch: {
    dummyDate(val) {
      this.extras.exp_BLS = this.formatDate(this.dummyDate);
    }
  },
  methods: {
    formatDate(date) {
      if (!date) return null;

      var [year, month, day] = date.split("-");
      day = day === undefined ? 1 : day;
      return `${day}/${month}/${year}`;
    },
    async submit() {
      if (this.$refs.extras_form.validate() === true) {
        try {
          let response = await this.$http.patch(
            "profile/" + this.extras.id + "/extras",
            this.extras
          );

          let profile = response.data.data;
          console.log(profile);

          this.$store.state.user.profile = profile;
          this.$store.dispatch("toast_me", {
            appear: true,
            title: "Extras. ",
            message: "Actualizado con éxito.",
            color: "primary"
          });
        } catch (error) {
          console.log(error);
        }
      } else {
        console.log("Aun hay faltantes");
      }
    }
  }
};
</script>
