<template>
  <v-content class="pa-0">
    <v-container fluid="fluid">
      <v-expansion-panel class="elevation-0 panel-suuma" expand v-model="panels">
        <v-expansion-panel-content >
          <template v-slot:header>
            <div>
              <p class="header-expansion">
                <i class="fas fa-user"></i>
                Información básica
              </p>
              
            </div>
          </template>
          <basic-info :personal="profile"></basic-info>
        </v-expansion-panel-content>
        <v-expansion-panel-content >
          <template v-slot:header>
            <div>
              <p class="header-expansion">
                <i class="fas fa-book-medical"></i>
                Contacto de emergencia
              </p>
              
            </div>
          </template>
          <emergency-contact :ec="ec"></emergency-contact>
        </v-expansion-panel-content>
        <v-expansion-panel-content >
          <template v-slot:header>
            <div>
              <p class="header-expansion">
                <i class="fas fa-wrench"></i>
                Dirección
              </p>
              
            </div>
          </template>
          <direction-info :direction="direction"></direction-info>
        </v-expansion-panel-content>
        <v-expansion-panel-content >
          <template v-slot:header>
            <div>
              <p class="header-expansion">
                <i class="fas fa-building"></i>
                Facturación
              </p>
              
            </div>
          </template>
          <fiscal-info :fiscal="fiscal"></fiscal-info>
        </v-expansion-panel-content>
        <v-expansion-panel-content >
          <template v-slot:header>
            <div>
              <p class="header-expansion">
                <i class="fas fa-info-circle"></i>
                Extras
              </p>
              
            </div>
          </template>
          <extras-info :extras="extras"></extras-info>
        </v-expansion-panel-content>
        <v-expansion-panel-content >
          <template v-slot:header>
            <div>
              <p class="header-expansion">
                <i class="fas fa-wrench"></i>
                Avanzado
              </p>
              
            </div>
          </template>
          <advanced-info></advanced-info>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-container>
  </v-content>
</template>


<script>
import BasicInfo from "./BasicInfo";
import Advanced from './Advanced';
import EmergencyContact from './EmergencyContact';
import Direction from './Direction';
import Fiscal from './Fiscal';
import Extras from './Extras';

export default {
  name: 'ProfileComponent',
  components: {
    "basic-info": BasicInfo,
    'advanced-info': Advanced,
    'emergency-contact': EmergencyContact,
    'direction-info': Direction,
    'fiscal-info': Fiscal,
    'extras-info': Extras
  },
  async created(){
    await this.fetchProfile();
  },
  data: () => ({
    profile: {},
    panels: [true, false, false, false, false, false],
  }),
  computed: {
    ec(){
      return {
        id: this.profile.id,
        name: this.profile.ec_name,
        appat: this.profile.ec_appat,
        apmat: this.profile.ec_apmat,
        relationship: this.profile.ec_relationship,
        telephone: this.profile.ec_telephone
      }
    },
    direction(){
      return {
        id: this.profile.id,
        street: this.profile.dir_street,
        state: this.profile.dir_state,
        suburb: this.profile.dir_col,
        postal_code: this.profile.dir_pc
      }
    },
    fiscal() {
      return {
        id: this.profile.id,
        business_name: this.profile.fis_business_name,
        direction: this.profile.fis_dir_fiscal,
        RFC: this.profile.fis_RFC,
        email: this.profile.fis_email,
      }
    },
    extras(){
      return {
        id: this.profile.id,
        generation: parseInt(this.profile.generation),
        exp_BLS: this.profile.BLS_exp
      }
    }
  },
  methods:{ 
    async fetchProfile() {
      try {
        let profile = await this.$http.get('profile');
        this.profile = profile.data.data ;
      } catch (error) {
        console.log(error);
        
      }
    },
  }
};
</script>

