<template>
  <v-content class="pa-0">
    <v-data-table
      class="elevation-0 suuma-table"
      :headers="headers"
      :items="usuarios"
      item-key="id"
    >
      <template v-slot:no-data>
        <v-alert :value="true" color="error" icon="warning">No hay usuarios por aprobar</v-alert>
      </template>
      <template v-slot:items="props">
        <td class="text-xs-left">
          {{ props.item.email }}
          <template v-if="props.item.email === myEmail.email ">
            <v-icon color="red" right>star</v-icon>
          </template>
        </td>
        <td class="text-xs-center">
          <v-flex xs12 sm6 d-flex>
            <v-select
              :disabled="(props.item.email === 'admin@suuma.com.mx') ? true : false"
              :items="ranges"
              v-model="props.item.range"
            ></v-select>
          </v-flex>
        </td>
        <td class="text-xs-center">
          <v-checkbox
            hide-details
            class="align-center justify-center"
            :disabled="(props.item.email === 'admin@suuma.com.mx') ? true : false"
            v-model="props.item.instructor"
          ></v-checkbox>
        </td>
        <td class="text-xs-center">
          <v-checkbox
            :disabled="(props.item.email === 'admin@suuma.com.mx') ? true : false"
            hide-details
            class="align-center justify-center"
            v-model="props.item.operator"
          ></v-checkbox>
        </td>
        <td class="text-xs-center">
          <v-checkbox
            :disabled="(props.item.email === 'admin@suuma.com.mx') ? true : false"
            hide-details
            class="align-center justify-center"
            v-model="props.item.service_chief"
          ></v-checkbox>
        </td>
        <td class="text-xs-center">
          <v-switch
            :disabled="(props.item.email === 'admin@suuma.com.mx') ? true : false"
            hide-details
            class="align-center justify-center"
            v-model="props.item.isActive"
          ></v-switch>
        </td>
        <td class="text-xs-center">
          <v-btn
            small
            depressed
            color="info"
            @click="patch(props.item.id)"
            :disabled="!props.item.changed"
          >Guardar</v-btn>
        </td>
        <td class="text-xs-center">
          <v-btn
            small
            depressed
            color="green darken-1"
            class="white--text"
            :disabled="(props.item.email === 'admin@suuma.com.mx') ? true : false"
            @click="fetchGroups(props.item)"
          >Roles</v-btn>
        </td>
      </template>
    </v-data-table>
    <div v-if="groups.length > 0">
      <role-dialog
        :open="rolesDialog"
        :started_roles="groups"
        :user_info="workingUser"
        :key="workingUser.id"
        @updateGroups="updateGroups"
        @dismiss="closeAndClean"
      ></role-dialog>
    </div>
  </v-content>
</template>


<script>
import RoleDialog from "./RoleDialog";
import { mapState } from "vuex";

export default {
  components: {
    "role-dialog": RoleDialog
  },
  async created() {
    this.$store.commit("SET_LOADING", {
      appear: true,
      message: "Obteniendo usuarios..."
    });
    await this.fetchUsers();
    this.$store.commit("SET_LOADING", { appear: false, message: "" });
  },
  data() {
    return {
      tipos_usuarios: ["Admin", "Operador"],
      ranges: ["TAM", "CP"],
      headers: [
        { text: "Correo", value: "email", sortable: false, align: "left" },
        {
          text: "Rango",
          value: "aprobar",
          sortable: false,
          align: "center",
          width: "1%"
        },
        {
          text: "Instructor",
          value: "instructor",
          sortable: false,
          align: "center"
        },
        {
          text: "Operador",
          value: "operator",
          sortable: false,
          align: "center"
        },
        {
          text: "Jefe de Servicio",
          value: "service_chief",
          sortable: false,
          align: "center"
        },
        // { text: "Grupo", value: "user_group", sortable: false },
        { text: "Activar", value: "approve", sortable: false, align: "center" },
        { text: "Acciones", value: "save", sortable: false, align: "center" },
        {
          text: "Avanzado",
          value: "advanced",
          sortable: false,
          align: "center"
        }
        // { text: "Avanzado", value: "advanced", sortable: false, align: "center"}
      ],
      usuarios: [],
      usuariosCopy: [],
      rolesDialog: false,
      workingUser: {},
      groups: [],
      defaultGroups: [
        { name: "basic", role: "Básico" },
        { name: "service_chief", role: "Jefe de Servicio" },
        { name: "operator", role: "Operador" },
        { name: "training", role: "Capacitación" },
        { name: "director", role: "Director" },
        { name: "operational_director", role: "Director Operacional" },
        { name: "operational_personel", role: "Personal Operativo" },
        { name: "admin", role: "Administrador" }
      ]
    };
  },
  computed: {
    ...mapState({
      myEmail: "user"
    })
  },
  watch: {
    usuarios: {
      handler: function(e) {
        if (e.length == 0) return;
        let changes = new Array(this.usuarios.length).fill(0);
        let id = 0;
        e.forEach(element => {
          let keys = Object.keys(element);
          for (const key in keys) {
            if (
              e[id][keys[key]] !== this.usuariosCopy[id][keys[key]] &&
              keys[key] != "changed"
            ) {
              changes[id] += 1;
            }
          }
          id += 1;
        });
        id = 0;
        for (const change of changes) {
          if (change != 0) {
            this.usuarios[id].changed = true;
          } else {
            this.usuarios[id].changed = false;
          }
          id += 1;
        }
      },
      deep: true
    }
  },
  methods: {
    async patch(id) {
      let id_real = 0;
      this.usuarios.some((el, idx) => {
        if (el.id === id) {
          id_real = idx;
          return true;
        }
      });
      try {
        let response = await this.$http.patch(
          "approvals/users/" + id,
          this.usuarios[id_real]
        );
        let user = response.data.data;
        this.usuarios[id_real].changed = false;
        this.usuariosCopy[id_real] = this.$deepCopy(this.usuarios[id_real]);
      } catch (error) {
        console.log(error);
      }
    },
    async fetchUsers() {
      try {
        let notActive = await this.$http.get("approvals/users");
        if (notActive.data.data.length > 0) {
          for (const user in notActive.data.data) {
            // Casting
            notActive.data.data[user].changed = false;
            notActive.data.data[user].instructor =
              notActive.data.data[user].instructor == 1 ? true : false;
            notActive.data.data[user].operator =
              notActive.data.data[user].operator == 1 ? true : false;
            notActive.data.data[user].service_chief =
              notActive.data.data[user].service_chief == 1 ? true : false;
            notActive.data.data[user].isActive =
              notActive.data.data[user].isActive == 1 ? true : false;
          }
          this.usuarios = notActive.data.data;
          // console.log(this.usuarios);

          this.usuariosCopy = this.$deepCopy(notActive.data.data);
        }
      } catch (error) {
        console.log(error);
      }
    },
    async fetchGroups(user) {
      try {
        let id = user.id;
        this.workingUser = user;
        let groups = await this.$http.get("/profile/" + id + "/groups");
        console.log(groups);

        this.groups = [];
        groups.data.data.forEach(element => {
          let role = {
            name: element.name,
            role: this.defaultGroups.filter(
              role => role.name == element.name
            )[0].role
          };
          this.groups.push(role);
        });
        this.rolesDialog = true;
      } catch (error) {
        console.log(error);
      }
    },
    async updateGroups(groups) {
      let newGroups = await this.$http.patch(
        "profile/" + this.workingUser.id + "/groups",
        {
          groups: groups
        }
      );
      if (this.$store.state.user.email === this.workingUser.email) {
        let newRoles = [];
        newGroups.data.data.forEach(group => {
          newRoles.push(group.name);
        });
        await this.$store.dispatch("update_self_roles", newRoles);
      }
      this.$store.dispatch("toast_me", {
        appear: true,
        title: "Información básica.",
        message: "Roles actualizados con éxito",
        color: "primary"
      });
      this.closeAndClean();
    },
    closeAndClean() {
      this.workingUser = {};
      this.groups = [];
      this.rolesDialog = false;
    }
  }
};
</script>

<style>
.fixSelect {
  /* overflow-y: auto; */
  overflow: scroll;
}
</style>
