<template>
  <v-layout wrap>
    <v-flex v-if="groups.some(group => group === 'admin')" xs12 class="text-xs-center">
      <v-btn
        small
        outline
        depressed
        class="white--text"
        color="blue"
        @click="newGuardDialog = true"
      >Nueva guardia</v-btn>
    </v-flex>
    <v-layout class="mb-1">
      <v-flex sm4 xs12 class="text-sm-left text-xs-center">
        <v-btn depressed flat color="info" @click="prev()">
          <v-icon dark left>keyboard_arrow_left</v-icon>Prev
        </v-btn>
      </v-flex>
      <v-flex sm4 xs12 class="text-xs-center">
        <h1>{{ actualMonth.charAt(0).toUpperCase() + actualMonth.slice(1) }} {{ actualYear}}</h1>
      </v-flex>
      <v-flex sm4 xs12 class="text-sm-right text-xs-center">
        <v-btn depressed flat color="blue" @click="next()">
          Next
          <v-icon dark right>keyboard_arrow_right</v-icon>
        </v-btn>
      </v-flex>
    </v-layout>

    <v-flex xs12 class="pl-4 pr-4">
      <v-sheet height="500">
        <v-calendar
          v-model="start"
          :type="type"
          color="primary"
          ref="guard_calendar"
          locale="es-MX"
        >
          <template v-slot:day="{ date }">
            <div class="single-event">
              <template v-for="(event, index) in eventsMap[date]">
                <v-menu
                  :key="event.id"
                  v-model="event.open"
                  :close-on-content-click="false"
                  :disabled="isPast(event.date)"
                  offset-x
                >
                  <template v-slot:activator="{ on }">
                    <div
                      v-if="!event.time"
                      v-ripple
                      :class="{'my-event' : event.type === 'event', 'my-guard' : event.type === 'guard', 'event':true, 'disabled-guard': isPast(event.date), 'available': event.status === 'available', 'medium-available': event.status === 'few', 'busy': event.status === 'busy'}"
                      v-on="on"
                    >
                      <v-flex d-flex>
                        <strong>{{ event.title }}</strong>
                        <template v-if="event.participate">
                          <v-icon class="icon-event">star</v-icon>
                        </template>
                      </v-flex>
                    </div>
                  </template>
                  <v-card
                    color="grey lighten-4"
                    min-width="350px"
                    max-width="600px"
                    flat
                    :key="event.id"
                  >
                    <v-toolbar depressed class="elevation-0" color="grey darken-3" dark>
                      <v-toolbar-title>
                        <template v-if="event.type == 'event'">Evento</template>
                        <template v-else>Guardia</template>
                        <strong>({{ event.title }}.)</strong>
                        <small>/{{event.date}}</small>
                      </v-toolbar-title>
                      <v-spacer></v-spacer>
                      <template v-if="groups.some(group => group === 'admin')">
                        <v-btn icon flat>
                          <v-icon>edit</v-icon>
                        </v-btn>
                        <v-btn icon fla @click="confirmationDialog = true; workingEvent = event">
                          <v-icon>delete_forever</v-icon>
                        </v-btn>
                      </template>
                    </v-toolbar>
                    <v-card-title primary-title>
                      <template v-if="!isPast(event.date)">
                        <template v-if="event.status === 'available' || event.status === 'few'">
                          <template v-if="!event.guard_user.some(u => u.email === me.email)">
                            <v-flex xs12>
                              <v-subheader>Nuevo tripulante</v-subheader>
                            </v-flex>
                            <v-flex xs8 pl-2 :key="event.id">
                              <v-select
                                :items="leftRoles(event)"
                                v-model="event.competitor"
                                return-object
                                label="Rol"
                                item-text="name"
                                @change="$forceUpdate()"
                              ></v-select>
                            </v-flex>
                            <v-flex xs4>
                              <v-btn
                                pl-1
                                block
                                :key="event.id + index"
                                :disabled="Object.keys(eventsMap[date][index].competitor).length === 0"
                                depressed
                                color="primary"
                                @click="subscribeGuard(event)"
                              >Unirme</v-btn>
                            </v-flex>
                          </template>
                        </template>
                      </template>

                      <v-flex xs12>
                        <v-subheader
                          xs12
                        >Tripulación (Ocupación {{ event.guard_user.length }}/{{ event.max_quota }})</v-subheader>
                      </v-flex>
                      <v-flex xs12 pl-4>
                        <table xs12 width="100%">
                          <tr>
                            <th style="text-align: left;">Usuario</th>
                            <th style="text-align: center;">Rol</th>
                          </tr>
                          <tr v-for="(user, i) in event.guard_user" :key="i">
                            <td>{{ user.email }}</td>
                            <td style="text-align: center;">
                              {{ user.pivot.role | crewName }}
                              <template
                                v-if="user.email === me.email"
                              >
                                <strong>(yo)</strong>
                              </template>
                            </td>
                          </tr>
                        </table>
                      </v-flex>
                      <v-flex xs12>
                        <template v-if="event.guard_user.some(u => u.email === me.email)">
                          <v-subheader>Salir de la guardia</v-subheader>
                          <v-flex d-flex pl-4>
                            <v-checkbox
                              v-model="event.out"
                              label="Entiendo las implicaciones de salirme de la guardia, haciendo llegar un correo."
                              @change="$forceUpdate()"
                            ></v-checkbox>
                            <v-btn
                              :disabled="!event.out"
                              depressed
                              color="error"
                              @click="unsubscribeGuard(event)"
                            >Salir</v-btn>
                          </v-flex>
                        </template>
                      </v-flex>
                    </v-card-title>
                    <v-card-actions></v-card-actions>
                  </v-card>
                </v-menu>
              </template>
            </div>
          </template>
        </v-calendar>
      </v-sheet>
    </v-flex>
    <v-layout row wrap pl-4 pt-2>
      <v-subheader>Opciones de visualización</v-subheader>
      <v-flex xs12>
        <v-checkbox v-model="showGuards" label="Mostrar guardias"></v-checkbox>
      </v-flex>
      <v-flex xs12>
        <v-checkbox v-model="showEvents" label="Mostrar eventos"></v-checkbox>
      </v-flex>
    </v-layout>

    <new-guard :open="newGuardDialog" @dismiss="closeAndClean" @save="saveNewGuard"></new-guard>

    <v-dialog v-model="confirmationDialog" persistent max-width="290">
      <v-card>
        <v-card-title class="headline">¿Borrar esta guardia?</v-card-title>
        <v-card-text>{{ workingEvent.type | eventOrGuard }} {{ workingEvent.date + ' ' + workingEvent.hour }} </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="success" depressed @click="confirmationDialog = false">Disagree</v-btn>
          <v-btn color="error" depressed @click="deleteGuard(workingEvent.id)">Agree</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
import moment from "moment";
import { mapGetters, mapMutations, mapState } from "vuex";
import NewGuardDialog from "./NewGuardDialog";
import { switchCase } from "babel-types";

moment.locale("es_MX");

export default {
  name: "GuardView",
  components: {
    "new-guard": NewGuardDialog
  },
  filters: {
    crewName(dbName) {
      let name = "";
      switch (dbName) {
        case "operator":
          name = "Operador";
          break;
        case "service_chief":
          name = "Jefe de servicio";
          break;
        case "personal":
          name = "Personal";
          break;
        default:
          name = "";
          break;
      }
      return name;
    },
    eventOrGuard(type) {
      if (type === "guard") return "Guardia";
      return "Evento";
    }
  },
  data: () => ({
    type: "month",
    actualMonth: moment().format("MMMM"),
    actualYear: moment().format("Y"),
    start: moment().format("YYYY-MM-DD"),
    newGuardDialog: false,
    showEvents: true,
    showGuards: true,
    events: [],
    confirmationDialog: false,
    workingEvent: {},
  }),
  computed: {
    ...mapGetters({
      groups: "getRoles"
    }),
    ...mapState({
      me: "user"
    }),
    eventsMap() {
      const map = {};
      this.events.forEach(e => {
        e.title = [].concat(e.hour.split(":")[0]) + " HRS";
        e.details = e.description;
        e.date = e.date.replace(/\//g, "-");
        e.open = false;
        e.participate = e.guard_user.some(e => e.email === this.me.email);
        e.competitor = {}; // PARA PARTICIPAR EN UNA GUARDIA NUEVA
        e.out = false; // EN CASO DE QUE DESEE SALIR DE LA GUARDIA
        let criteria = e.guard_user.length / parseFloat(e.max_quota);
        if (criteria < 0.5) {
          e.status = "available";
        }
        if (criteria > 0.5 && criteria < 1.0) {
          e.status = "few";
        }
        if (criteria == 1.0) {
          e.status = "busy";
        }
        if (this.showEvents && e.type == "event") {
          (map[e.date] = map[e.date] || []).push(e);
        }
        if (this.showGuards && e.type == "guard") {
          (map[e.date] = map[e.date] || []).push(e);
        }
      });
      return map;
    },
    currentTime() {
      return moment(this.start, "YYYY-MM-DD");
    }
  },
  async created() {
    this.setLoading({
      appear: true,
      message: "Obteniendo guardias..."
    });
    await this.fetchUnits();
    await this.fetchGuards(
      moment().format("YYYY/MM/") + "01",
      moment()
        .add(1, "M")
        .format("YYYY/MM/") + "01"
    );
    this.setLoading({
      appear: false,
      message: ""
    });
  },
  methods: {
    ...mapMutations({
      setLoading: "SET_LOADING"
    }),
    async fetchUnits() {
      let units = await this.$http.get("units");
      this.units = units.data.data;
    },
    async fetchGuards(initDate, lastDate) {
      let guards = await this.$http.get("guards", {
        params: {
          initDate: initDate,
          lastDate: lastDate
        }
      });
      this.events = guards.data.data;
    },
    leftRoles(event) {
      let availableRoles = [];

      let userRoles = {
        operator: this.me.operator,
        service_chief: this.me.service_chief
      };

      let usedRoles = {
        operator: 0,
        service_chief: 0,
        personal: 0
      };

      event.guard_user.forEach(u => {
        usedRoles[u.pivot.role] = usedRoles[u.pivot.role] + 1;
      });

      if (usedRoles.operator === 0 && userRoles.operator === 1) {
        availableRoles.push({
          role: "operator",
          name: "Operador"
        });
      }

      if (usedRoles.service_chief === 0 && userRoles.service_chief === 1) {
        availableRoles.push({
          role: "service_chief",
          name: "Jefe de servicio"
        });
      }

      let op = parseInt(event.max_quota) - 2 - usedRoles.personal;

      if (op >= 1) {
        availableRoles.push({
          role: "personal",
          name: "Personal"
        });
      }

      // console.log(event.date, event.max_quota, op, event.guard_user, availableRoles);

      return availableRoles;
    },
    isPast(stringDate) {
      return moment(stringDate, "YYYY-MM-DD").isBefore(moment(), "day");
    },
    async prev() {
      this.setLoading({
        appear: true,
        message: "Obteniendo guardias..."
      });
      let actual = moment(this.start, "YYYY-MM-01");
      let before = moment(this.start, "YYYY-MM-01");
      await this.fetchGuards(
        before.subtract(1, "M").format("YYYY/MM/") + "01",
        actual.format("YYYY/MM/") + "01"
      );
      this.updateTime(this.currentTime.subtract(1, "M"));
      this.$refs.guard_calendar.prev();
      this.setLoading({
        appear: false,
        message: ""
      });
    },
    async next() {
      this.setLoading({
        appear: true,
        message: "Obteniendo guardias..."
      });

      let actual = moment(this.start, "YYYY-MM-01").add(1, "M");
      await this.fetchGuards(
        actual.format("YYYY/MM/") + "01",
        actual.add(1, "M").format("YYYY/MM/") + "01"
      );
      this.updateTime(this.currentTime.add(1, "M"));
      this.$refs.guard_calendar.next();
      this.setLoading({
        appear: false,
        message: ""
      });
    },
    updateTime(time) {
      this.actualMonth = time.format("MMMM");
      this.actualYear = time.format("Y");
    },
    open(event) {
      alert(event.title);
    },
    async subscribeGuard(event) {
      let user_id = this.me.id;
      let guard_id = event.id;
      this.setLoading({
        appear: true,
        message: "Actualizando guardia..."
      });
      let subscribe = await this.$http.patch(
        "guards/" + guard_id + "/subscribe/" + user_id,
        {
          role: event.competitor.role,
          isActive: 1
        }
      );
      let actual = moment(this.start, "YYYY-MM-01");
      await this.fetchGuards(
        actual.format("YYYY/MM/") + "01",
        actual.add(1, "M").format("YYYY/MM/") + "01"
      );
      this.setLoading({
        appear: false,
        message: ""
      });
    },
    async unsubscribeGuard(event) {
      let user_id = this.me.id;
      let guard_id = event.id;
      this.setLoading({
        appear: true,
        message: "Actualizando guardia..."
      });
      let unsubscribe = await this.$http.patch(
        "guards/" + guard_id + "/unsubscribe/" + user_id,
        {
          role: event.competitor.role,
          isActive: 1
        }
      );
      let actual = moment(this.start, "YYYY-MM-01");
      await this.fetchGuards(
        actual.format("YYYY/MM/") + "01",
        actual.add(1, "M").format("YYYY/MM/") + "01"
      );
      this.setLoading({
        appear: false,
        message: ""
      });
    },
    async saveNewGuard(event) {
      this.setLoading({
        appear: true,
        message: "Guardando guardia..."
      });
      let newGuard = await this.$http.post("guards", event);
      let actual = moment(this.start, "YYYY-MM-01");
      await this.fetchGuards(
        actual.format("YYYY/MM/") + "01",
        actual.add(1, "M").format("YYYY/MM/") + "01"
      );
      this.setLoading({
        appear: false,
        message: ""
      });
      this.closeAndClean();
    },
    async deleteGuard(id){
      this.setLoading({
        appear: true,
        message: "Eliminando guardia..."
      });
      let deletedGuard = await this.$http.post('guards/' + id);
      console.log(deletedGuard);
      let actual = moment(this.start, "YYYY-MM-01");
      await this.fetchGuards(
        actual.format("YYYY/MM/") + "01",
        actual.add(1, "M").format("YYYY/MM/") + "01"
      );
      this.setLoading({
        appear: false,
        message: ""
      }); 
      this.confirmationDialog = false;
      this.workingEvent = {};
    },
    closeAndClean() {
      this.newGuardDialog = false;
    }
  }
};
</script>

