  border: 2.5px solid #29c24a;
}
.medium-available {
  border: 2.5px solid #e9c62b;
}
.busy {
  border: 2.5px solid #181818;
}
.single-event {
  margin-top: 0.5em;
}
.guard  {
  background-color: #a10c0c;
}
.event {
  background-color: #f09917;
}
.icon-event {
  text-align: right;
  font-size: 16px !important;
  align-items: baseline;
  display: inline;
}
.element-list {
  list-style: none;
}
.v-calendar-weekly__head-weekday {
  text-transform: uppercase;
