<template>
  <div>
    <v-divider></v-divider>
    <v-card class="mb-4 elevation-0">
      <v-card-text>
        <v-layout row d-flex wrap pa-2>
          <v-flex xs12 pa-2>
            <v-subheader>Información de contacto</v-subheader>
            <v-form ref="emergency_contact" lazy-validation v-model="emergency_contact_form">
              <v-flex d-flex>
                <v-text-field
                  xs12
                  md4
                  required
                  clearable
                  label="Nombre(s)"
                  :rules="rules.name"
                  v-model="ec.name"
                ></v-text-field>
                <v-text-field
                  xs12
                  md4
                  required
                  clearable
                  label="Apellido Paterno"
                  :rules="rules.appat"
                  v-model="ec.appat"
                ></v-text-field>
                <v-text-field xs12 md4 clearable label="Apellido Materno" v-model="ec.apmat"></v-text-field>
              </v-flex>
              <v-flex d-flex>
                <v-select :items="parentescos" label="Parentesco" v-model="ec.relationship"></v-select>
                <v-text-field
                  xs12
                  md6
                  required
                  clearable
                  :rules="rules.telephone"
                  label="Telefono"
                  mask="phone"
                  v-model="ec.telephone"
                ></v-text-field>
              </v-flex>
            </v-form>
          </v-flex>
        </v-layout>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn class="mr-4" depressed color="primary" @click="submit">Guardar</v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
export default {
  name: "EmergencyContact",
  props: {
    ec: {
      type: Object,
      default: function() {
        return {
          id: "",
          name: "",
          appat: "",
          apmat: "",
          relationship: "",
          telephone: ""
        };
      }
    }
  },
  data: () => ({
    parentescos: ['Padre/Madre', 'Pareja', 'Hermano', 'Hijo', 'Tio', 'Otro'],
    rules: {
      name: [v => !!v || "Nombre es obligatorio"],
      appat: [v => !!v || "Apellido Paterno es obligatorio"],
      relationship: [v => !!v || "Imperativo saber parentesco"],
      telephone: [v => !!v || "Telefono es obligatorio"]
    },
    emergency_contact_form: false
  }),
  methods: {
    async submit() {
      if (this.$refs.emergency_contact.validate() === true) {
        try {
          let response = await this.$http.patch(
            "profile/" + this.ec.id + "/ec",
            this.ec
          );

          let profile = response.data.data;

          this.$store.state.user.profile = profile;
          this.$store.dispatch("toast_me", {
            appear: true,
            title: "Contacto de Emergencia.",
            message: "Actualizado con éxito",
            color: "primary"
          });
        } catch (error) {
          console.log(error);
        }
      } else {
        console.log("Aun hay faltantes");
      }
    }
  }
};
</script>
