<template>
  <div>
    <v-divider></v-divider>
    <v-card class="mb-4 elevation-0">
      <v-card-text>
        <v-layout row d-flex wrap pa-2>
          <v-flex xs12 md4 pa-2>
            <v-list two-line subheader>
              <v-subheader>General</v-subheader>
              <v-list-tile avatar>
                <v-list-tile-avatar>
                  <img
                    class="profilePhoto"
                    v-if="photo === ''"
                    src="@/assets/blank.png"
                    alt="Foto de Perfil"
                  />
                  <img class="profilePhoto" :src="photo" alt="Logo" />
                </v-list-tile-avatar>
                <v-list-tile-content @click="photoChanger = true">
                  <v-list-tile-title>Foto de Perfil</v-list-tile-title>
                  <v-list-tile-sub-title>Click para cambiar</v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
            </v-list>
          </v-flex>
          <v-flex xs12 md4>
            <v-subheader>Personal</v-subheader>
            <v-flex class="pa-2">
              <v-form ref="basic" lazy-validation v-model="forms.basic">
                <v-text-field
                  required
                  clearable
                  label="Nombre(s)"
                  v-model="personal.name"
                  :rules="rules.name"
                ></v-text-field>
                <v-flex d-flex>
                  <v-text-field
                    xs6
                    required
                    clearable
                    label="Apellido Paterno"
                    v-model="personal.appat"
                    :rules="rules.appat"
                  ></v-text-field>
                  <v-text-field
                    xs6
                    required
                    clearable
                    label="Apellido Materno"
                    v-model="personal.apmat"
                    :rules="rules.apmat"
                  ></v-text-field>
                </v-flex>
                <v-text-field clearable label="CURP" v-model="personal.CURP" :rules="rules.CURP"></v-text-field>
                <v-menu
                  v-model="datePicker"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  lazy
                  transition="scale-transition"
                  offset-y
                  full-width
                  min-width="290px"
                >
                  <template v-slot:activator="{ on }">
                    <v-text-field
                      required
                      v-model="personal.date_birth"
                      label="Fecha de nacimiento"
                      prepend-icon="event"
                      hint="DD/MM/AAAA"
                      :rules="rules.date"
                      v-on="on"
                    ></v-text-field>
                  </template>
                  <v-date-picker v-model="dummyDate" @input="datePicker = false"></v-date-picker>
                </v-menu>
                <v-select
                  required
                  v-model="personal.blood_type"
                  :items="blood_types"
                  label="Tipo de sangre"
                ></v-select>
              </v-form>
            </v-flex>
          </v-flex>
          <v-flex xs12 md4>
            <v-form ref="contact" lazy-validation v-model="forms.contact">
              <v-flex class="pa-2">
                <v-subheader>Contacto</v-subheader>
                <v-text-field
                  clearable
                  required
                  label="Celular"
                  v-model="personal.tel_contact"
                  :rules="rules.telephone"
                  mask="phone"
                ></v-text-field>
                <v-text-field clearable label="Otro teléfono" mask="phone" v-model="personal.tel_contact_2"></v-text-field>
              </v-flex>
            </v-form>
          </v-flex>
        </v-layout>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn class="mr-4" depressed color="primary" @click="submit">Guardar</v-btn>
      </v-card-actions>
    </v-card>

    <v-dialog v-model="photoChanger" persistent width="500">
      <v-card class="pa-2">
        <v-card-title class="headline">Foto de perfil</v-card-title>
        <v-card-text class="text-xs-center">
          <h3>Previsualización</h3>
          <br />
          <!-- <img class="ma-1" :src="image.url" height="250" v-if="image.url" />
          <img class="text-xs-center" src="@/assets/blank.png" alt="Default" height="250" v-else />-->
          <img
            class="profilePhoto"
            v-if="image.url === ''"
            :src="photo"
            alt="Foto de Perfil"
            height="250"
          />
          <img class="profilePhoto" v-else :src="image.url" alt="Logo" height="250" />
          <v-text-field
            label="Seleccione imagen"
            @click="pickFile"
            v-model="image.name"
            prepend-icon="attach_file"
          ></v-text-field>
          <input
            type="file"
            style="display: none"
            ref="image"
            accept="image/x-png, image/gif, image/jpeg"
            @change="onFilePicked"
          />
          <v-divider></v-divider>
          <v-subheader>Condiciones</v-subheader>
          <div class="text-xs-left">
            <ul>
              <li>Las dimensiones de la imagen no pueden pasar de 500px x 500px.</li>
              <li>Formatos aceptados: *.jpeg, *.gif, *.png</li>
            </ul>
          </div>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn flat @click="photoChanger = false">Cancelar</v-btn>
          <v-btn color="primary" flat @click="submitPhoto">Cambiar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "basic-info",
  props: {
    personal: {
      type: Object,
      default: function() {
        return {
          name: "",
          appat: "",
          apmat: "",
          CURP: "",
          date_birth: "",
          blood_type: "",
          tel_contact: "",
          tel_contact_2: ""
        };
      }
    }
  },
  computed: {
    ...mapState({
      photo: "photo"
    })
  },
  data: () => ({
    dialog: false,
    photoChanger: false,
    datePicker: false,
    dummyDate: "",
    forms: {
      basic: false,
      contact: false
    },
    image: {
      name: "",
      url: "",
      file: "",
      dimensions: {
        h: "",
        w: ""
      }
    },
    rules: {
      name: [v => !!v || "El nombre es requerido"],
      appat: [v => !!v || "El A Paterno es requerido"],
      apmat: [v => !!v || "El A Materno es requerido"],
      CURP: [v => (v || "").length > 8 || "Más de 8 caracteres"],
      telephone: [v => !!v || "Un número de contacto al menos"],
      date: [v => !!v || "Fecha de nacimiento es obligatoria"]
    },
    blood_types: [
      "A+",
      "A-",
      "B+",
      "B-",
      "AB+",
      "AB-",
      "O+",
      "O-",
      "Desconocido"
    ],
    menu: false,
    menu2: false,
    contacto: {
      e_nombre: "",
      e_appat: "",
      e_apmat: "",
      e_parentesco: "",
      e_telefono: ""
    }
  }),
  watch: {
    dummyDate(val) {
      this.personal.date_birth = this.formatDate(this.dummyDate);
    }
  },
  methods: {
    formatDate(date) {
      if (!date) return null;

      const [year, month, day] = date.split("-");
      return `${day}/${month}/${year}`;
    },
    async submit() {
      if (
        this.$refs.basic.validate() === true &&
        this.$refs.contact.validate() === true
      ) {
        try {
          let response = await this.$http.patch(
            "profile/" + this.personal.id + "/basic",
            this.personal
          );

          let profile = response.data.data;
          // console.log(profile);
          this.$store.state.user.profile = profile;
          this.$store.dispatch("toast_me", {
            appear: true,
            title: 'Información básica.',
            message: "Actualiza con éxito",
            color: "primary"
          });
          //   window.setTimeout(this.$store.commit('HIDE_TOAST'), 6000);
        } catch (error) {
          console.log(error);
        }
      } else {
        console.log("No están completas");
      }
    },
    pickFile() {
      this.$refs.image.click();
    },
    onFilePicked(e) {
      // Idea took from https://jsfiddle.net/meyubaraj/fLbe7r72/
      const files = e.target.files;
      if (files[0] !== undefined) {
        this.image.name = files[0].name;
        if (this.image.name.lastIndexOf(".") <= 0) {
          return;
        }
        const fr = new FileReader();
        fr.readAsDataURL(files[0]);
        let su = this.image.dimensions;
        fr.onload = function(file) {
          var image = new Image();
          image.src = file.target.result;
          image.onload = function() {
            su.h = this.height;
            su.w = this.width;
          };
        };
        fr.addEventListener("load", () => {
          this.image.url = fr.result;
          this.image.file = files[0]; // this is an image file that can be sent to server...
        });
      } else {
        this.image.name = "";
        this.image.file = "";
        this.image.url = "";
      }
    },
    async submitPhoto() {
      if (this.image.dimensions.h / this.image.dimensions.w !== 1) {
        console.log("No es cuadrada");
        return;
      }
      try {
        const img = new FormData();
        img.append("photo", this.image.file);
        this.$store.commit("SET_LOADING", {
          appear: true,
          message: "Subiendo..."
        });
        let response = await this.$http.post("profile/photo", img);
        console.log(response);
        this.$store.commit("SET_LOADING", { appear: false, message: "" });
        this.photoChanger = false;
        this.$store.state.photo = this.image.url;
        this.image = {
          name: "",
          dimensions: { h: "", w: "" },
          file: null,
          url: ""
        };
      } catch (error) {
        console.log(error);
      }
    }
  }
};
</script>
<style>
.v-input {
  margin: 5px;
}
</style>
