<template>
  <v-dialog v-model="open" persistent max-width="600px">
    <v-card class="pa-2">
      <v-card-title class="headline">Usuario ({{ user_info.email }})</v-card-title>
      <v-card-text>
        <v-layout wrap>
          <v-flex xs12>
            <p>
              A continuación, elige los roles que puede tener el usuario. Por defecto, todos son
              <strong>usuarios básicos.</strong>
            </p>
            <h4 class="mb-2">Roles actuales:</h4>
          </v-flex>
          <v-flex class="mb-3">
            <v-chip v-for="role in selected_roles" :key="role.name" color="blue" text-color="white">
              <v-avatar class="blue darken-4">{{ role.role.slice(0,1).toUpperCase() }}</v-avatar>
              {{ role.role }}
            </v-chip>
          </v-flex>
          <v-flex xs12>
            <h4 class="mb-2">Roles disponibles:</h4>
            <v-combobox
              v-model="selected_roles"
              :items="items"
              label="Roles disponibles"
              chips
              small-chips
              clearable
              prepend-icon="supervised_user_circle"
              solo
              multiple
              item-text="role"
              item-value="name"
            >
              <template v-slot:selection="data">
                <v-chip
                  :selected="data.selected"
                  color="grey lighten-4"
                  close
                  @input="remove(data.item)"
                >
                  <v-avatar class="grey lighten-1">{{ data.item.role.slice(0,1).toUpperCase() }}</v-avatar>
                  <strong>{{ data.item.role }}</strong>&nbsp;
                  <span>({{ data.item.name }})</span>
                </v-chip>
              </template>
            </v-combobox>
          </v-flex>
        </v-layout>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="red darken-1" flat @click="closeDialog">Close</v-btn>
        <v-btn color="blue darken-1" flat @click="updateRoles">Save</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "RoleDialog",
  props: {
    open: {
      type: Boolean,
      default: false
    },
    started_roles: {
      type: Array,
      default: function() {
        return [];
      }
    },
    user_info: {
      type: Object,
      default: function() {
        return {
          id: 0
        }
      }
    }
  },
  data: () => ({
    selected_roles: [],
    items: [
      { name: "basic", role: "Básico" },
      { name: "service_chief", role: "Jefe de Servicio" },
      { name: "operator", role: "Operador" },
      { name: "training", role: "Capacitación" },
      { name: "director", role: "Director" },
      { name: "operational_director", role: "Director Operacional" },
      { name: "operational_personel", role: "Personal Operativo" },
      { name: "admin", role: "Administrador" }
    ]
  }),
  created() {
    console.log("Opening RoleDialog");
    this.selected_roles = this.started_roles;
  },
  methods: {
    remove(item) {
      this.selected_roles.splice(this.selected_roles.indexOf(item), 1);
      this.selected_roles = [...this.selected_roles];
    },
    closeDialog() {
      this.$emit("dismiss");
    },
    updateRoles() {
      this.$emit("updateGroups", this.selected_roles);
    }
  }
};
</script>
